header .header-top .top-menu ul {
    margin: 0px;
    padding: 0px;
}
header .header-top .top-menu ul li {
    display: inline-block;
    padding: 0px 10px;
    border-right: 1px solid #777777;
}
header .header-top .top-menu ul li:last-child {
    border-right: none;
}
header .header-top .top-menu ul li a {
    font: normal normal normal 12px/15px "Swis721 BT";
    letter-spacing: 0px;
    color: #777777;
    opacity: 1;
    text-decoration: none;
}
header .header-top .top-right-menu {
    text-align: right;
}
header .header-top .top-right-menu ul {
    margin: 0px;
    padding: 0px;
}
header .header-top .top-right-menu ul li {
    display: inline-block;
}
header .header-top .top-right-menu ul li a {
    background: #19858f 0% 0% no-repeat padding-box;
