header .header-top .top-right-menu ul li a i {
    margin-right: 5px;
}

header .header-bottom .logo img {
    width: 100%;
}
header .header-bottom .logo {
    padding: 20px 0;
}
header .header-bottom .main-menu {
    margin: 35px 0 35px 100px;
    text-align: right;
}
header .header-bottom .main-menu ul {
    margin: 0px;
    padding: 0px;
}
header .header-bottom .main-menu ul li {
    display: inline-block;
}
header .header-bottom .main-menu ul li a {
    font: normal normal normal 14px/22px "Swis721 BT";
    letter-spacing: 0px;
    color: #777777;
    text-decoration: none;
    display: flex;
    text-align: left;
}
header .header-bottom .main-menu ul li a i {
    padding: 10px;
    background: #f0efef 0% 0% no-repeat padding-box;
    border-radius: 11px;
    font-size: 30px;
    margin-right: 7px;
    float: left;
    color: #19858f;
}

header .header-bottom .main-menu ul li.icon-m {
    background-repeat: no-repeat;
    padding-left: 54px;
